import Link from 'next/link'
import React, { Component } from 'react';
import amino from '../../img/disease/amino.svg'
import dynamic from 'next/dynamic'
const AminoComponent = dynamic(() => import('./amino-acid'))
class disease extends Component {
	state = {
		i: 0
	}
	renderContent(){
		console.log('i=', this.state.i);
	
		if (this.state.i == 0) {
			return (<div><AminoComponent /></div>)
		}
		else if (this.state.i == 1) {
			return (<div>XXXXX</div>)
		}
	}
	ippp = () => {
		
	}
	render() {
		return (
			<div className="container-fluid">
				<div className="diseasecomponent">
					<div className="row"></div>
					<h2 className="pt-5 pb-5 pl-5 pr-5">
						<strong>โรคและวิธีการรักษา</strong>
					</h2>
					<div className="row">
						<div className="col-xs-12 col-sm-12 col-md-12 col-lg-4">
							<div className="container-fluid">
								<div className="row">
									<div className="col-xs-12 col-sm-6 col-md-6 col-lg-12">
										<div id="disease-content" className="border mt-5 pl-1 pr-1 pt-1 pb-1" onClick={this.setState({i : 1})}>
											<div className="row">
												<div className="col-xs-6 col-sm-3 col-md-3 col-lg-2 d-flex justify-content-center align-items-center">
													<img src={amino} width="45px" />
												</div>
												<div className="col-xs-6 col-sm-9 col-md-9 col-lg-10">
													<h5 className="pt-2 pl-2 pb-2 ">กรดอะมิโน</h5>
												</div>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
						<div className="col-xs-12 col-sm-12 col-md-12 col-lg-8">
							{this.renderContent()}
						</div>
					</div>
				</div>
			</div>

		)
	}

}
export default disease



